"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import Cookies from "js-cookie";
import { FaUserMd, FaLock, FaEye, FaEyeSlash, FaHospitalSymbol } from "react-icons/fa";
import { motion } from "framer-motion";

const DEFAULT_CREDENTIALS = {
  username: "admin",
  password: "medecin123"
};

export default function LoginPage() {
  const [credentials, setCredentials] = useState({
    username: "",
    password: ""
  });
  const [showPassword, setShowPassword] = useState(false);
  const [error, setError] = useState("");
  const router = useRouter();

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();
    setError("");

    if (!credentials.username || !credentials.password) {
      setError("Veuillez remplir tous les champs");
      return;
    }

    if (credentials.username === DEFAULT_CREDENTIALS.username && 
        credentials.password === DEFAULT_CREDENTIALS.password) {
      // Stocker dans un cookie avec expiration
      Cookies.set("isLoggedIn", "true", { 
        expires: 1, // Expire dans 1 jour
        secure: process.env.NODE_ENV === "production",
        sameSite: "strict"
      });
      
      router.push("/");
    } else {
      setError("Identifiants incorrects");
      setCredentials(prev => ({ ...prev, password: "" }));
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gradient-to-br from-blue-50 to-teal-50 p-4">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="w-full max-w-md"
      >
        <div className="bg-white rounded-xl shadow-xl overflow-hidden">
          {/* En-tête médical */}
          <div className="bg-gradient-to-r from-blue-600 to-teal-600 p-6 text-center">
            <div className="flex justify-center mb-3">
              <FaHospitalSymbol className="text-4xl text-white" />
            </div>
            <h1 className="text-2xl font-bold text-white">Portail Médical Sécurisé</h1>
            <p className="text-blue-100 mt-1">Accès réservé au personnel médical</p>
          </div>

          <form onSubmit={handleLogin} className="p-8">
            {error && (
              <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-4 p-3 bg-red-100 border-l-4 border-red-500 text-red-700 rounded"
              >
                <p>{error}</p>
              </motion.div>
            )}

            <div className="mb-6">
              <label className="block text-gray-700 mb-2 font-medium">Identifiant</label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <FaUserMd className="text-gray-400" />
                </div>
                <input
                  type="text"
                  placeholder="Votre identifiant"
                  value={credentials.username}
                  onChange={(e) => setCredentials({...credentials, username: e.target.value})}
                  className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition"
                  required
                />
              </div>
            </div>

            <div className="mb-6">
              <label className="block text-gray-700 mb-2 font-medium">Mot de passe</label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <FaLock className="text-gray-400" />
                </div>
                <input
                  type={showPassword ? "text" : "password"}
                  placeholder="Votre mot de passe"
                  value={credentials.password}
                  onChange={(e) => setCredentials({...credentials, password: e.target.value})}
                  className="w-full pl-10 pr-12 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition"
                  required
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute inset-y-0 right-0 pr-3 flex items-center text-gray-500 hover:text-gray-700"
                >
                  {showPassword ? <FaEyeSlash /> : <FaEye />}
                </button>
              </div>
            </div>

            <motion.button
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              type="submit"
              className="w-full bg-gradient-to-r from-blue-600 to-teal-600 text-white py-3 rounded-lg font-medium shadow-md hover:shadow-lg transition-all"
            >
              Se connecter
            </motion.button>

            <div className="mt-6 text-center text-sm text-gray-500">
              <p>En vous connectant, vous acceptez notre politique de confidentialité.</p>
              <p className="mt-2 text-xs">
                Identifiants par défaut:{" "}
                <span className="font-mono bg-gray-100 px-2 py-1 rounded">
                  {DEFAULT_CREDENTIALS.username}
                </span>{" "}
                /{" "}
                <span className="font-mono bg-gray-100 px-2 py-1 rounded">
                  {DEFAULT_CREDENTIALS.password}
                </span>
              </p>
            </div>
          </form>
        </div>

        <div className="mt-6 text-center text-xs text-gray-500">
          <p>© {new Date().getFullYear()} Système de Gestion Médicale</p>
          <p className="mt-1">Version 1.0.0</p>
        </div>
      </motion.div>
    </div>
  );
}